/* Login Page Styles */
.login-page {
  min-height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;

  position: relative;
  /* Allow avatar to overflow the top of the card so it isn't clipped */
  overflow: visible;
}

.login-page::before {
  content: '';
  position: absolute;
  width: 40%;
  height: 40%;
  background: rgba(255, 255, 255, 0.1);
  top: -20%;
  right: -20%;
  border-radius: 50%;
}

.login-page::after {
  content: '';
  position: absolute;
  width: 30%;
  height: 30%;
  background: rgba(255, 255, 255, 0.1);
  bottom: -15%;
  left: -15%;
  border-radius: 50%;
}

.login-container {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
}

.login-form-container {
  position: relative;
  width: 100%;
  max-width: 420px;
}

.avatar-wrap {
  position: absolute;
  /* nudge the avatar slightly higher so it's visually above the card */
  top: -60px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10;
}

.login-avatar {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 4px solid white;
  background: white;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.2);
}

.card {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 20px;
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.2);
  padding: 3rem 2rem 1.5rem;
  position: relative;
  overflow: hidden;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.login-title {
  text-align: center;
  color: #2d3748;
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 2rem;
}

.error-message {
  background: #fed7d7;
  padding: 0.8rem;
  border-radius: 8px;
  margin-bottom: 1rem;
  text-align: center;
  border: 1px solid #feb2b2;
}

.success-message {
  background: #c6f6d5;
  color: #276749;
  padding: 0.8rem;
  border-radius: 8px;
  margin-bottom: 1rem;
  text-align: center;
  border: 1px solid #9ae6b4;
}

.card-body {
  margin-bottom: 1.5rem;
}

.form-group {
  position: relative;
  margin-bottom: 1.5rem;
}

.form-icon {
  position: absolute;
  left: 18px; /* slight inset from the input border */
  top: 50%;
  transform: translateY(-50%);
  color: #718096;
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: none;
  width: 20px;
  height: 20px;
}

.form-icon svg {
  width: 20px;
  height: 20px;
  display: block;
  stroke-width: 1.4px;
}
.form-input {
  box-sizing: border-box;
  width: 100%;
  padding: 12px 12px 12px 56px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  font-size: 0.95rem;
  transition: all 0.3s ease;
  background: rgba(255, 255, 255, 0.95);
  color: #2d3748;
  height: 44px;
  backdrop-filter: blur(10px);
}

.form-input::placeholder {
  color: #94a3b8;
}

.form-input:focus {
  outline: none;
  border-color: #667eea;
  background: white;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

/* Name fields layout: first and last name side-by-side */
.name-grid {
  display: flex;
  gap: 12px;
  margin-bottom: 1rem;
}
.name-grid .form-group {
  flex: 1;
}
.form-group.half {
  flex: 1;
}

.form-input-password {
  padding-right: 3rem;
}

.eye-toggle-btn {
  position: absolute;
  right: 12px;
  top: 50%;
  transform: translateY(-50%);
  background: none;
  border: none;
  font-size: 1.2rem;
  cursor: pointer;
  color: #718096;
  padding: 0.2rem;
}

.eye-toggle-btn:hover {
  color: #667eea;
}

.options-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 1rem;
}

.remember {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: #4a5568;
  font-size: 0.9rem;
  cursor: pointer;
}

.remember input {
  width: 16px;
  height: 16px;
}

.forgot-btn {
  background: none;
  border: none;
  color: #667eea;
  font-size: 0.9rem;
  cursor: pointer;
  text-decoration: underline;
}

.forgot-btn:hover {
  color: #764ba2;
}

/* Footer with three buttons */
.card-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  margin-top: 2rem;
  padding-top: 1.5rem;
  border-top: 1px solid #e2e8f0;
}

.home-btn {
  background: #e2e8f0;
  color: #4a5568;
  border: none;
  padding: 0.8rem 1.5rem;
  border-radius: 8px;
  text-decoration: none;
  font-weight: 600;
  font-size: 0.9rem;
  transition: all 0.3s ease;
  text-align: center;
  flex: 1;
}

.home-btn:hover {
  background: #cbd5e0;
  color: #2d3748;
  transform: translateY(-1px);
}

.login-submit-btn {
  background: #00D9B2;
  color: white;
  border: none;
  padding: 0.8rem 2rem;
  border-radius: 8px;
  font-weight: 700;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  flex: 2;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.login-submit-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(102, 126, 234, 0.4);
}

.register-switch-btn {
  background: #48bb78;
  color: white;
  border: none;
  padding: 0.8rem 1.5rem;
  border-radius: 8px;
  font-weight: 600;
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.3s ease;
  flex: 1;
}

.register-switch-btn:hover {
  background: #38a169;
  transform: translateY(-1px);
  box-shadow: 0 4px 15px rgba(72, 187, 120, 0.4);
}

/* Responsive Design */
  @media (max-width: 480px) {
    .form-input {
      padding-left: 48px; /* slightly smaller on very small screens */
    }

    .form-icon {
      left: 14px;
      width: 18px;
      height: 18px;
    }

    .form-icon svg {
      width: 18px;
      height: 18px;
    }
  }
@media (max-width: 768px) {
  .login-container {
    padding: 1rem;
  }
  
  .card {
    padding: 2.5rem 1.5rem 1rem;
  }
  
  .login-avatar {
    width: 80px;
    height: 80px;
  }
  
  .card-footer {
    flex-direction: column;
    gap: 0.8rem;
  }
  
  .home-btn,
  .login-submit-btn,
  .register-switch-btn {
    width: 100%;
    flex: none;
  }
  
  .options-row {
    flex-direction: column;
    gap: 0.8rem;
    align-items: flex-start;
  }
}